.container {
  width: 80em;
  margin: auto;
  background-color: cadetblue;  
  height: 90vh;
  display: flex;
  flex-direction: row;
}

.heading {
  width: 100%;
  font-size: 20px;
  font-weight: 600;
  display: flex;
  justify-content: center;
  height: 4rem;
  align-items: center;
  border-bottom: solid thin #ccc;
  background-color: rgb(214, 214, 179);
}

.people {
  width: 30%;
  background-color: beige;
}

.chatBox {
  width: 70%;
  display: flex;
  flex-direction: column;
}

.window {
  margin: 0.6rem 1rem;  
  max-height: 80vh;
  overflow: scroll;
  overflow-x: hidden;
}

.chatTitle {
  width: 100%;
  font-weight: 600;
  font-size: 18px;
  color: azure;
  height: 3rem;
  display: flex;
  align-items: center;
  padding: 0 1rem 0 1rem;
  border-bottom: solid thin white;
}

.message {
  max-width: max-content;
  border-radius: 2px;
  background-color: white;
  padding: 0.7rem 1rem;
  font-weight: 600;
  margin-bottom: 1rem;
}

.editMessagebox{
  width: 100%;
  margin-top: auto;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  padding: 0.3rem;
}

.editMessage{
  width: 80%;
  height: 2rem;
  border-radius: 1rem;
  border: none;
  outline: none;
  resize: none;
  padding: 0.3rem 1rem 0.3rem 1rem;
  font-weight: 600;
}

.postButton {
  border: none;
  outline: none;
  border-radius: 2px;
  cursor: pointer;
  font-weight: 400;
}